import React from 'react';
import { Modal, ModalHeader,ModalBody,Button,ModalFooter } from 'reactstrap';
//import './ItemAddedModal.css';
import BModal from "./thanksmodal";
import {InsertAPI} from "../action/insert-action";
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
const axios = require('axios');

class ItemAddedModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modalOpen: false,
      success:false,
      selectedStar:0,
      remarks:'',
      data:[],
      caption:['Poor!','Fair!','Average!','Good!','Excellent!']
    }
    console.log(this.props)
    //this.onInsert=this.onInsert.bind(this)
    //this.props.common.insertAPI=this.props.common.insertAPI.bind(this);
  };

  toggle = () => {
    this.setState({modalOpen:!this.state.modalOpen});
  }

  clickStar=(e)=> {
    this.setState({ selectedStar: e.target.id })
  }

  onChange=(e)=>{
    this.setState({ [e.target.name]: e.target.value })
  }

  handleOpen = () => {
    this.setState({ 
      modalOpen: true 
    },() => {setTimeout(this.handleClose(), 3000)});
    // setTimeout(this.handleClose(), 3000);
  };

  handleClose = () => {
    this.setState({ 
      modalOpen: this.props.open || false,
      data:this.props.data[0]
    });
    // setTimeout(this.handleClose(), 3000);
  };
  componentDidMount()
  {
    this.handleOpen();
  }
 async onSubmit(type,data){
    let ignore=1
    if(type==='rate')
      ignore=0
    
    axios.post(global.serverURL + "/api/courier/DriverRating",  {
        "bookingid": data.BookingId,"usermobile":data.Mobileno,"drivermobile":data.Drivermobile,
        "rating":this.state.selectedStar,"remarks":this.state.remarks,"isignore":ignore
      }
      )
      .then((response) => {
        console.log(response)
        if(response.status===200)
        {
          if(ignore===0)
            this.setState({modalOpen:!this.state.modalOpen,success:true})
          else this.setState({modalOpen:false})
        }
        return response.data;
      }).catch(error => {
        console.log(error);
      });
  }

  render () {
    let stars=[]
    const faPadding={padding:'5px',color:'#007bff'}
    let i=1
    let j=this.state.selectedStar
    let cap=this.state.caption[j-1]

    for(i=1;i<=j;i++)
    {
      stars.push(
        <i id={i} style={faPadding} className="fa fa-star fa-2x primary" onClick={this.clickStar}></i>
        )
    }
    let k= Number(j)+1
    for(i=k;i<=5;i++)
    {
      stars.push(
        <i id={i} style={faPadding} className="fa fa-star-o fa-2x primary" onClick={this.clickStar}></i>
        )
    }
    let res= (
        <div>
      <Modal isOpen={this.state.modalOpen} className={this.props.className}>
      <ModalBody>
      <div className="row">      
      <div className="col-12 text-center">
      
      <h5 className="mb-4">Rate Rider</h5>
      <img class="profile-user-img img-fluid img-circle"
      src="dist/img/avatar5.png"
      alt="User profile picture" />
      <h3 className="profile-username text-center">Mr. {this.state.data.DriverName}</h3>
      <p className="text-muted text-center">Madmoney Driver</p>
      </div>

      <div className="col-12 text-center">
      {stars}
      <div style={faPadding} className="mb-3">{cap}</div>

      <textarea name="remarks" className="form-control" placeholder="Drop your comments..!" onChange={this.onChange} />
      </div>

      </div>
      </ModalBody>
      <ModalFooter>
      <Button color="primary" onClick={()=>this.onSubmit('rate',this.state.data)}>Rate Now!</Button>{' '}
      <Button color="secondary" onClick={()=>this.onSubmit('skip',this.state.data)}>Skip</Button>
      </ModalFooter>

      </Modal>
      </div>
    )
    if(this.state.success)
      return <BModal open={true} />
    else return res;
    };
  }

  const mapStateFromProps =state=>{
    return state;
  }

  const mapActionFromProps =(dispatch,props)=>{
    return {
      onInsert:(url,data)=>dispatch(InsertAPI(url,data))
    }
  }
  export default connect(mapStateFromProps,mapActionFromProps)(ItemAddedModal)